<template><section>
<h1>单选框</h1>
<p>基本组件-单选框。主要用于一组可选项单项选择，或者单独用于切换到选中状态。</p>
<h2 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-radio v-model=\&quot;value\&quot; label=\&quot;123\&quot;&gt;&lt;/xl-radio&gt;\n&lt;p&gt;{{value}}&lt;/p&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-radio v-model="value" label="123"></xl-radio>
<p>{{value}}</p>
</div>
                            <div slot="desc"><p>基础的使用方式</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'value'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'123'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="jin-yong-dan-xuan-kuang"><a class="header-anchor" href="#jin-yong-dan-xuan-kuang" aria-hidden="true">¶</a> 禁用单选框</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-radio v-model=\&quot;value\&quot; label=\&quot;123\&quot; disabled&gt;&lt;/xl-radio&gt;\n&lt;p&gt;{{value}}&lt;/p&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-radio v-model="value" label="123" disabled></xl-radio>
<p>{{value}}</p>
</div>
                            <div slot="desc"><p>使用<code>disabled</code>禁用单选框</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'value'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'123'</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="zhi-du-dan-xuan-kuang"><a class="header-anchor" href="#zhi-du-dan-xuan-kuang" aria-hidden="true">¶</a> 只读单选框</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-radio v-model=\&quot;value\&quot; label=\&quot;123\&quot; readonly&gt;&lt;/xl-radio&gt;\n&lt;p&gt;{{value}}&lt;/p&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-radio v-model="value" label="123" readonly></xl-radio>
<p>{{value}}</p>
</div>
                            <div slot="desc"><p>使用<code>readonly</code>只读单选框</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'value'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'123'</span> <span class="hljs-attr">readonly</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="dai-bian-kuang"><a class="header-anchor" href="#dai-bian-kuang" aria-hidden="true">¶</a> 带边框</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-radio v-model=\&quot;value\&quot; label=\&quot;123\&quot; bordered&gt;&lt;/xl-radio&gt;\n&lt;xl-radio v-model=\&quot;value\&quot; label=\&quot;123\&quot; bordered readonly&gt;&lt;/xl-radio&gt;\n&lt;xl-radio v-model=\&quot;value\&quot; label=\&quot;123\&quot; bordered disabled&gt;&lt;/xl-radio&gt;\n&lt;p&gt;{{value}}&lt;/p&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-radio v-model="value" label="123" bordered=""></xl-radio>
<xl-radio v-model="value" label="123" bordered="" readonly></xl-radio>
<xl-radio v-model="value" label="123" bordered="" disabled></xl-radio>
<p>{{value}}</p>
</div>
                            <div slot="desc"><p>使用<code>bordered</code>设置</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'value'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'123'</span> <span class="hljs-attr">bordered</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'value'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'123'</span> <span class="hljs-attr">bordered</span> <span class="hljs-attr">readonly</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'value'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'123'</span> <span class="hljs-attr">bordered</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="ding-zhi-zhi"><a class="header-anchor" href="#ding-zhi-zhi" aria-hidden="true">¶</a> 定制值</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-radio label=\&quot;123\&quot; true-value=\&quot;TRUE\&quot; false-value=\&quot;FALSE\&quot; v-model=\&quot;customValue\&quot;&gt;&lt;/xl-radio&gt;\n&lt;p&gt;{{customValue}}&lt;/p&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-radio label="123" true-value="TRUE" false-value="FALSE" v-model="customValue"></xl-radio>
<p>{{customValue}}</p>
</div>
                            <div slot="desc"><p>可以设置<code>true-value</code>和<code>false-value</code></p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'123'</span> <span class="hljs-attr">true-value</span>=<span class="hljs-string">'TRUE'</span> <span class="hljs-attr">false-value</span>=<span class="hljs-string">'FALSE'</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'customValue'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{customValue}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="dan-xuan-kuang-zu"><a class="header-anchor" href="#dan-xuan-kuang-zu" aria-hidden="true">¶</a> 单选框组</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-radio-group v-model=\&quot;groupValue\&quot;&gt;\n    &lt;xl-radio label=\&quot;aaa\&quot;&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;bbb\&quot;&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;ccc\&quot;&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;ddd\&quot;&gt;&lt;/xl-radio&gt;\n&lt;/xl-radio-group&gt;\n&lt;xl-radio-group v-model=\&quot;groupValue\&quot;&gt;\n    &lt;xl-radio value=\&quot;123\&quot; label=\&quot;aaa\&quot;&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;bbb\&quot;&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;ccc\&quot;&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;ddd\&quot;&gt;&lt;/xl-radio&gt;\n&lt;/xl-radio-group&gt;\n&lt;xl-radio-group v-model=\&quot;groupValue\&quot;&gt;\n    &lt;xl-radio value=\&quot;123\&quot; label=\&quot;aaa\&quot; disabled&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;bbb\&quot;&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;ccc\&quot;&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;ddd\&quot; readonly&gt;&lt;/xl-radio&gt;\n&lt;/xl-radio-group&gt;\n&lt;p&gt;{{groupValue}}&lt;/p&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-radio-group v-model="groupValue">
    <xl-radio label="aaa"></xl-radio>
    <xl-radio label="bbb"></xl-radio>
    <xl-radio label="ccc"></xl-radio>
    <xl-radio label="ddd"></xl-radio>
</xl-radio-group>
<xl-radio-group v-model="groupValue">
    <xl-radio value="123" label="aaa"></xl-radio>
    <xl-radio label="bbb"></xl-radio>
    <xl-radio label="ccc"></xl-radio>
    <xl-radio label="ddd"></xl-radio>
</xl-radio-group>
<xl-radio-group v-model="groupValue">
    <xl-radio value="123" label="aaa" disabled></xl-radio>
    <xl-radio label="bbb"></xl-radio>
    <xl-radio label="ccc"></xl-radio>
    <xl-radio label="ddd" readonly></xl-radio>
</xl-radio-group>
<p>{{groupValue}}</p>
</div>
                            <div slot="desc"><p>使用<code>xl-radio-group</code>实现单选框组</p>
</div>
