    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'aaa'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'bbb'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'ccc'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'ddd'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-radio-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'groupValue'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'123'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'aaa'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'bbb'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'ccc'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'ddd'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-radio-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'groupValue'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'123'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'aaa'</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'bbb'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'ccc'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'ddd'</span> <span class="hljs-attr">readonly</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{groupValue}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="an-niu-yang-shi-de-dan-xuan-kuang-zu"><a class="header-anchor" href="#an-niu-yang-shi-de-dan-xuan-kuang-zu" aria-hidden="true">¶</a> 按钮样式的单选框组</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-radio-group v-model=\&quot;groupValue\&quot; type=\&quot;button\&quot;&gt;\n    &lt;xl-radio label=\&quot;aaa\&quot;&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;bbb\&quot;&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;ccc\&quot;&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;ddd\&quot;&gt;&lt;/xl-radio&gt;\n&lt;/xl-radio-group&gt;\n&lt;br&gt;\n&lt;xl-radio-group v-model=\&quot;groupValue\&quot; type=\&quot;button\&quot;&gt;\n    &lt;xl-radio label=\&quot;aaa\&quot; disabled&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;bbb\&quot;&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;ccc\&quot; readonly&gt;&lt;/xl-radio&gt;\n    &lt;xl-radio label=\&quot;ddd\&quot;&gt;&lt;/xl-radio&gt;\n&lt;/xl-radio-group&gt;\n&lt;p&gt;{{groupValue}}&lt;/p&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-radio-group v-model="groupValue" type="button">
    <xl-radio label="aaa"></xl-radio>
    <xl-radio label="bbb"></xl-radio>
    <xl-radio label="ccc"></xl-radio>
    <xl-radio label="ddd"></xl-radio>
</xl-radio-group>
<br>
<xl-radio-group v-model="groupValue" type="button">
    <xl-radio label="aaa" disabled></xl-radio>
    <xl-radio label="bbb"></xl-radio>
    <xl-radio label="ccc" readonly></xl-radio>
    <xl-radio label="ddd"></xl-radio>
</xl-radio-group>
<p>{{groupValue}}</p>
</div>
                            <div slot="desc"><p>使用<code>type='button'</code>实现单选框组</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-radio-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'groupValue'</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'button'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'aaa'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'bbb'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'ccc'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'ddd'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-radio-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'groupValue'</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'button'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'aaa'</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'bbb'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'ccc'</span> <span class="hljs-attr">readonly</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'ddd'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{groupValue}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
