<h2 id="radio-shu-xing"><a class="header-anchor" href="#radio-shu-xing" aria-hidden="true">¶</a> radio 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>radio 显示值</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>name</td>
<td>radio 的 name（单独使用时有效）</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>value</td>
<td>radio 单独使用时，支持 v-model 绑定</td>
<td>string / number / boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>true-value</td>
<td>选中时的 value 值（单独使用时有效）</td>
<td>string / number / boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>false-value</td>
<td>未选中时的 value 值（单独使用时有效）</td>
<td>string / number / boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>border</td>
<td>是否显示 radio 边框</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>readonly</td>
<td>是否只读</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用状态</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="radio-shi-jian"><a class="header-anchor" href="#radio-shi-jian" aria-hidden="true">¶</a> radio 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>radio 改变事件</td>
<td>选中时的值（trueValue）</td>
</tr>
<tr>
<td>on-focus</td>
<td>radio 获得焦点事件</td>
<td>表单元素事件 e</td>
</tr>
<tr>
<td>on-blur</td>
<td>radio 失去焦点事件</td>
<td>表单元素事件 e</td>
</tr>
</tbody>
</table>
<h2 id="radio-slots"><a class="header-anchor" href="#radio-slots" aria-hidden="true">¶</a> radio slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>radio 显示的 label，如果没有设置插槽，默认使用 label 参数，如果 label 也没有，就为空</td>
</tr>
</tbody>
</table>
<h2 id="radio-group-shu-xing"><a class="header-anchor" href="#radio-group-shu-xing" aria-hidden="true">¶</a> radio group 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>选中的 radio，支持 v-model 绑定</td>
<td>string / number / boolean</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>name</td>
<td>单选框组的名称 ，默认随机生成</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>type</td>
<td>单选框组类型</td>
<td>string</td>
<td>button</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2 id="radio-group-shi-jian"><a class="header-anchor" href="#radio-group-shi-jian" aria-hidden="true">¶</a> radio group 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>radio 选中事件</td>
<td>选中时的 radio 的值</td>
</tr>
</tbody>
</table>
<h2 id="radio-group-slots"><a class="header-anchor" href="#radio-group-slots" aria-hidden="true">¶</a> radio group slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>用来设置 radio 组</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default{
        data(){
            return{
                value:false,
                groupValue:'bbb',
                customValue:''
            }
        }
    }
</script>


